export default {
	name: 'calcNumber',
	data() {
		return {}
	},
	props: ['count','size'],
	methods: {
		handleReduce(count) {
			count--;
			if(count <= 1) {
				count = 1;
			}
			this.$emit('update:count', count)
		},
		handlePlus(count) {
			count++;
			this.$emit('update:count', count)
		},
	}
}



