import Vue from 'vue'
import Router from 'vue-router'
import Login from '../views/Login'
import Register from '../views/Register'
import ForgetPassword from '../views/Forgetpassword'
import Mine from '../views/Mine'
import Setting from '../views/Setting'
import ModifyPassword from '../views/ModifyPassword'
import HelppingCenter from '../views/HelppingCenter'
import Profile from '../views/Profile'
import Index from '../views/Index'
import Materials from '../views/Materials'
import Worker from '../views/Worker'
import MaterialsDetail from '../views/MaterialsDetail'
import GoodsDetail from '../views/GoodsDetails'
import OrderConfirm from '../views/OrderConfirm'
Vue.use(Router)
const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/worker',
      name: 'Worker',
      component: Worker
    },
    {
      path: '/materials',
      name: 'Materials',
      component: Materials
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/forgetPassword',
      name: 'forgetPassword',
      component: ForgetPassword
    },
    {
      path: '/mine',
      name: 'mine',
      component: Mine
    },
    {
      path: '/setting',
      name: 'setting',
      component: Setting
    },
    {
      path: '/modifyPassword',
      name: 'modifyPassword',
      component: ModifyPassword
    },
    {
      path: '/helppingCenter',
      name: 'helppingCenter',
      component: HelppingCenter
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile
    },
    {
      path: '/materialsDetail',
      name: 'materialsDetail',
      component: MaterialsDetail
    },
    {
      path: '/goodsDetail',
      name: 'goodsDetail',
      component: GoodsDetail
    },
    {
      path: '/orderConfrim',
      name: 'orderConfirm',
      component: OrderConfirm
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(res => res.meta.requireAuth)) {
    if (localStorage.getItem('token')) {
      next()
    } else {
      next({
        path: '/login',
        query: {redirect: to.fullPath}
      })
    }
  } else {
    next()
  }
})

export default router




