
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import CalcNumber from '../components/CalcNumber.vue'
const imgList = [{
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
}, {
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
}, {
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
}]
export default {
name: 'goodsDetails',
data() {
return {
swiperList: imgList,
swiperItemIndex: 1,
goodsInfo: {
name: 'MX9965-D1*108+0.5*36-和韵-米金 这行显示全称',
price: 23413,
count: 1,
},
relatedWidth:400,//关联商品的父节点宽度
relatedList: [{ //关联商品
name: 'MX9965-D1*108 + 0.5 * 36-和韵-米金 这行显示全称',
price: 23413,
},{
name: 'MX9965-D1*108 + 0.5 * 36-和韵-米金 这行显示全称',
price: 23413,
},{
name: 'MX9965-D1*108 + 0.5 * 36-和韵-米金 这行显示全称',
price: 23413,
}]
}
},
components: {
Swiper,
SwiperItem,
CalcNumber,
Tab,
TabItem,
Badge,XButton,XHeader
},
methods: {
calcRelatedWidth() {
var tempWidth = 0;
for(var i = 0; i < this.relatedList.length; i++) {
tempWidth += this.$refs.relatedBox.children[i].clientWidth + 5;
}
return tempWidth;
},
},
mounted() {
this.relatedWidth = this.calcRelatedWidth();
}
}



